import axios from 'axios';
import vue from 'vue'

export default {
    /**
     * 获取指定字典的值
     * @param {*} name 
     * @param {*} url 
     */
     async getDictList(dictTypeList, o, successFun) {
        let data = {
            dictTypeList : dictTypeList
        };

        await axios.post('/tool/api/dict/getDictList', data, {
            headers: {'content-type': 'application/json;charset=UTF-8'}
        }).then((res) => {
            console.log(res);

            for (let index in dictTypeList) {
                let dictType = dictTypeList[index];
                o[dictType] = res.data.data[dictType];
            }
        }).catch((error, res) => {
            // 请求dao失败dao
            console.log(error)
            vue.prototype.$toast({text: '获取字典项失败', type: 'danger'});
        }); 

        successFun();
    }
}