import axios from 'axios';
import toast from '../toast';
import vue from 'vue'

export default {
    mounted() {
      toast();
    },
  /**
   * 在地址查询参数
   * @param {*} name 
   * @param {*} url 
   */
  getUrlKey(name,url) {
  　return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ""])[1].replace(/\+/g, '%20')) || null
  },
  /**
   * 在地址查询参数
   * @param {在请求参数获取数据} name 
   */ 
  getParams(name, defaultValue) {
    let url = window.location.href;
    let result = this.getUrlKey(name, url);
    if (result == null && defaultValue != null) {
      result = defaultValue;
    }
    return result;
  },
  /**
   * 判断是否为空
   */ 
  isEmpty(str) {
    return str == null || str === "";
  },
  /**
   * 判断是否为空
   * @param needQuestionMark 是否需要问号
   */ 
  buildGetParams(data, needQuestionMark) {
    let paramStr = "";
    let hasParams = false;

    for(var key in data){
      if (!this.isEmpty(data[key])) {
        if (hasParams) {
          paramStr += '&';
        } else if (needQuestionMark) {
          paramStr += '?';
        }
        
        paramStr += key + '=' + data[key];
        hasParams = true;
      }
    }

    return paramStr;
  },
  /**
   * 数据复制
   */
  deepCopy(data) {
    return JSON.parse(JSON.stringify(data));
  },
  /**
   * 数据复制
   */
  copyProperty(form, target, exclude) {
    for (let obj in target) {
      if (exclude != null && exclude.indexOf(obj) >= 0) {
        continue;
      } else {
        target[obj] = form[obj];
      }
    }
  },
  /**
   * 下载文件
   * @param {*} URL 下载地址
   * @param {*} datas 数据
   * @param {*} file_type 文件类型
   * @param {*} afterDownload 成功回调
   */
  downloadFile(URL, datas, file_type, afterDownload) {
    axios.post(URL,datas, {responseType: 'arraybuffer'})
      .then(res => {
        let pdfUrl = window.URL.createObjectURL(new Blob([res.data],{type: file_type}));
        if (res.headers["content-disposition"] == null || res.headers["content-disposition"].split(";").length == 0) {
          console.log(res);
          vue.prototype.$toast({text: '下载失败', type: 'danger'});
          return;
        }

        let temp = res.headers["content-disposition"].split(";")[1].split("filename=")[1];
        let s= /\"/g
        temp = temp.replace(s,'')
        let iconv = require('iconv-lite');
        iconv.skipDecodeWarning = true;//忽略警告
        temp = iconv.decode(temp, 'gbk');
        const link = document.createElement('a');
        link.href = pdfUrl;
        link.setAttribute('download', temp);
        document.body.appendChild(link);
        link.click()
        document.body.removeChild(link)

        if (afterDownload != null) {
          afterDownload();
        }
      }).catch(error => {
        console.log(error)
      });
  },
}