<template>

  <div class="panel panel-default">
    <a href="index.html">&nbsp;返回</a>
    <div class="panel-body">
      <br/>&nbsp;
      <div class="row">
        &nbsp;&nbsp;&nbsp;
        <div class="col-sm-2">
            <el-upload class="upload-idCardFile"
              action="/tool/api/excel/getExcelInfo"
              multiple
              :on-success="uploadSuccess"
              accept=".xls,.xlsx"
              :data="{}"
              :show-file-list="false">
              <el-button class="identify-btn" size="small" type="primary">导入</el-button>
            </el-upload>
        </div>
      </div>
      <br/>&nbsp;
      <button type="button" class="btn btn-primary btn-lg" @click="clear()">清空</button>
        &nbsp;&nbsp;&nbsp;
      <button type="button" class="btn btn-primary btn-lg" @click="openExportModel()">导出文件</button>
        &nbsp;&nbsp;&nbsp;
      <br/>
      <br/>

      <table class="table table-bordered">
        <thead class="listData">
          <tr class="info">
            <td colspan=1></td>
            <td colspan=4>文件名</td>
            <td colspan=1>操作</td>
          </tr>
        </thead>
        <tbody class="listData">
          <tr v-for="(item) in dataList" :key="item.name">
            <td colspan=1 width="5%"><input type="checkbox" v-model="item.checked"/></td>
            <td colspan=4 width="20%">{{item.name}}</td>
            <td colspan=1 width="30%">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <button type="button" class="btn btn-primary" @click="toTop(item)">置顶</button>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <button type="button" class="btn btn-primary" @click="getSheetModal(item)">sheet 页</button>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <button type="button" class="btn btn-danger" @click="del(item)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- 选择Sheet信息 -->
      <div class="modal fade" id="sheetModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title" id="myModalLabel">选择sheet 信息</h4>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">×</span>
              </button>
            </div>
            <div class="modal-body">
              <table class="table table-bordered">
                <thead class="listData">
                  <tr class="info">
                    <td colspan=1></td>
                    <td colspan=4>名称</td>
                    <td colspan=1>操作</td>
                  </tr>
                </thead>
                <tbody class="listData">
                  <tr v-for="(item) in sheetList" :key="item.name">
                    <td colspan=1 width="5%"><input type="checkbox" v-model="item.checked"/></td>
                    <td colspan=4 width="80%">{{item.name}}</td>
                    <td colspan=1 width="20%"><button type="button" class="btn btn-primary" @click="toTopSheet(item)">置顶</button></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

      <!-- 导出页面 -->
      <CombineExport ref="combineExport" :excelDataList="dataList"/>
    </div>
    
    
    <div class="modal fade" id="successModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="alert alert-success">
          <strong>{{successMsg}}</strong>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery';
  import Paper from "@/components/pageHelper";
  import CombineExport from '../excel/combineExport.vue';
  export default {
    name: "combine",
    data: function () {
      return {
         fileInputData: {
           filePath: "",
           fileNameList: [],
         },
         dataList : [
           
         ],
         fileMd5 : {
           
         },
         sheetList : [
           
         ],
         successMsg : ""
      }
    },
    components: {
      Paper,
      CombineExport
    },
    mounted() {

    },
    methods: {
      /**
       * 清空数据
       */
      clear() {
        this.dataList = [];
        this.fileMd5 = {};
      },
      /**
       * 打开sheet 页面
       */
      getSheetModal(itemData) {
        this.sheetList = itemData.sheetList;
        $('#sheetModal').modal('show')
      },
      /**
       * 打开导出配置 页面
       */
      openExportModel() {
        $('#combineExportModal').modal('show')
      },
      /**
       * 置顶 - 表格
       */
      toTop(itemData) {
          for (let index in this.dataList) {
            let data = this.dataList[index];

            if (itemData.md5 == data.md5 
              && itemData.name == data.name) {
              this.dataList.splice(index, 1);
              this.dataList.splice(0, 0, data);
            }
          }
      },
      /**
       * 置顶 sheet
       */
      toTopSheet(itemData) {
          for (let index in this.sheetList) {
            let data = this.sheetList[index];

            if (itemData.name == data.name) {
              this.sheetList.splice(index, 1);
              this.sheetList.splice(0, 0, data);
            }
          }
      },
      /**
       * 置顶 sheet
       */
      add(itemData) {
          for (let index in this.sheetList) {
            let data = this.sheetList[index];

            if (itemData.name == data.name) {
              this.sheetList.splice(index, 1);
              this.sheetList.splice(0, 0, data);
            }
          }
      },
      /**
       * 删除
       */
      del(itemData) {
          var index= this.dataList.findIndex(item =>{
            if(item.md5 == itemData.md5 && item.name == itemData.name) {
              return true;
            }
          });
          
          this.dataList.splice(index,1);
          this.fileMd5[itemData.name + "_" + itemData.md5] = null;
      },
      /**
       * 上传成功回调
       */
      uploadSuccess(res) {
        for (let index in res.data) {
          let data = res.data[index];
          if (this.fileMd5[data.name + "_" + data.md5] != null) {
            continue;
          }

          data.checked = true;
          data.sheetList = [];

          for (let sheetIndex in data.sheetNameList) {
            let sheetInfo = {};
            sheetInfo.checked = true;
            sheetInfo.name = data.sheetNameList[sheetIndex];
            data.sheetList.push(sheetInfo);
          }
          data.sheetNameList = null;

          this.dataList.push(data);
          this.fileMd5[data.name + "_" + data.md5] = 1;
        }
      },
    }
  }
</script>
<style type="text/css">
  .listData>tr>td {
    text-align:center; /*设置dao水平居中*/
    vertical-align:middle;/*设置垂直居zhi中*/
  }
</style>